.bk-ui-progressbar {
	height: 2em;
	text-align: left;
	overflow: hidden;
}
.bk-ui-progressbar .bk-ui-progressbar-value {
	margin: -1px;
	height: 100%;
}
.bk-ui-progressbar .bk-ui-progressbar-overlay {
	background: url("data:image/gif;base64,");
	height: 100%;
	filter: alpha(opacity=25); /* support: IE8 */
	opacity: 0.25;
}
.bk-ui-progressbar-indeterminate .bk-ui-progressbar-value {
	background-image: none;
}
